import React from 'react';
import axios from "axios";

class UsersWithAge extends React.Component {
    constructor(){
        super();
        this.state = {
            data: [],
        }
        this.getUsers = this.getUsers.call(this);
    }

    getUsers(){
        axios.get("/api/users/")
        .then(response => {
            console.log(response);
            this.setState({data: response.data})
        });
        
    }

    render() {
        return (
            <>
                <h1>All Users</h1>
                <h6>Users and thier Age</h6>
                <table className="table">
                <tbody>
                {this.state.data.map(( userProfile, index ) => {
                        return (
                            <tr key={'name'-index}>
                            <td>{userProfile.username}</td>
                            <td>{userProfile.age}</td>
                            </tr>
                        );
                    })}
                    </tbody>
                </table>
            </>
        );
    }
}

export default UsersWithAge;