import React, {useState} from 'react';
import axios from 'axios'

function AgeWithDemographics() {
    const [data, setData] = useState([]);    
    const [item, setItem] = useState('____');

    const itemSelect = (event) => {
        setItem(event.target.value);
        getAgesWithItemCount(event.target.value);
    }

    const getAgesWithItemCount = async(item) => {
        const response = await axios.get(`api/users/age?item=${item}`);
        setData(response.data)
    }

    
    return (
        <div>
            <h1>Age Demographics of Users with {item}</h1>
            <select className="form-select" id="item-list" onChange={itemSelect} value={item}>
                <option selected>----</option>
                <option  value="carrot">carrot</option>
                <option  value="apple">apple</option>
                <option  value="grapes">grapes</option>
                <option  value="cake">cake</option>
                <option  value="tv">tv</option>
                <option  value="crackers">crackers</option>
                <option  value="chips">chips</option>
                <option  value="ham">ham</option>
            </select>
            
            <table className="table">
                <thead>
                    <tr >
                    <th colScope="col"> Age </th>
                    <th colScope="col"> Count </th>
                    </tr>
                </thead>
                <tbody>
                    {data.map(( ageCount, index ) => {
                        return (
                            <tr key={'age'-index}>
                            <td colScope="col">{ageCount.age}</td>
                            <td colScope="col">{ageCount.count}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </div>
    )
            
}

export default AgeWithDemographics;